import comHeader from '@/components/header/header'
import { getIdCard } from '@/api/certification'
import Debounce from '@/util/debounce'
export default {
  name: 'certificationIdCard',
  components: {
    comHeader
  },
  props: {},
  data () {
    return {
      userReal: '',
      idCard: '',
      faceImg: '',
      idCardImg: '',
      idCardBackImg: ''
    }
  },
  methods: {
    handleSave () {
      const rules = {
        userReal: {
          name: "真实姓名",
          type: "validName",
          value: this.userReal,
          required: !0
        },
        idCard: {
          name: "身份证号",
          type: "validId",
          value: this.idCard,
          required: !0
        }
      }
      if (!this.$utils.dataValidityCheck(rules)) return
      Debounce(() => this._onSave(), 1000)
    },
    async _onSave () {
      let params = {
        userReal: this.userReal,
        idCard: this.idCard,
        faceImg: this.faceImg,
        idCardImg: this.idCardImg,
        idCardBackImg: this.idCardBackImg
      }
      getIdCard(params).then(({ data: { code, data, msg } }) => {
        if (code === 1) {
          this.$vux.toast.text('保存成功')
          this.$router.back(-1)
        }
      }).catch(() => {
        this.$vux.toast.text('网络出错，请稍后重试')
      })
    }
  }
}



